import React, { SFC } from 'react';
import { CustomArrowProps } from 'react-slick';

import { ArrowRight, ArrowLeft, ArrowUp, ArrowDown } from '../../common/icons';

type Props = {
  type: 'next' | 'prev' | 'up' | 'down';
  slidesToShow: number;
} & CustomArrowProps;

const svgProps = {
  height: 28,
  width: 28,
};

const arrows = {
  up: ArrowUp,
  down: ArrowDown,
  prev: ArrowLeft,
  next: ArrowRight,
};

export const CarouselArrow: SFC<Props> = (props: Props) => {
  const { onClick, type, slidesToShow, currentSlide, slideCount } = props;

  if (
    (['prev', 'up'].includes(type) && currentSlide === 0) ||
    (['next', 'down'].includes(type) && currentSlide + slidesToShow >= slideCount)
  )
    return null;

  const ArrowComponent = arrows[type];
  return (
    <button onClick={onClick} className={['next', 'down'].includes(type) ? 'slick-next' : 'slick-prev'}>
      <ArrowComponent {...svgProps} />
    </button>
  );
};
