import { BREAKPOINTS } from '../../common/utils';

const createSliderSettings = (slidesToScroll: number, slidesToShow: number, speed: number) => ({
  slidesToScroll,
  slidesToShow,
  speed,
});

export const SETTINGS_MAP = {
  [BREAKPOINTS.XLARGE]: createSliderSettings(7, 7, 1000),
  [BREAKPOINTS.LARGE]: createSliderSettings(6, 6, 900),
  [BREAKPOINTS.MEDIUM]: createSliderSettings(5, 5, 800),
  [BREAKPOINTS.SMALL]: createSliderSettings(4, 4, 700),
  [BREAKPOINTS.XSMALL]: createSliderSettings(3, 3, 500),
  [BREAKPOINTS.XXSMALL]: createSliderSettings(2, 2, 400),
};

const WIDTH_LIST = Object.keys(SETTINGS_MAP)
  .reverse()
  .map((w) => parseInt(w, 10));

export const DEFAULT_SETTINGS = createSliderSettings(1, 1, 200);
export const getBreakpoint = (width: number) => WIDTH_LIST.find((w) => width >= w);
