import React, { ComponentType, PureComponent, ErrorInfo } from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';

import { TipserError } from './tipser-error';
import { getDisplayName } from '../utils';

export const withSentry = <P extends {}>(Component: ComponentType<P>) => {
  const ComponentWithSentry = class extends PureComponent<P> {
    static displayName = `WithSentry(${getDisplayName(Component)})`;

    componentDidCatch(error: Error, errorInfo: ErrorInfo) {
      throw new TipserError(error, errorInfo);
    }

    render() {
      return <Component {...(this.props as P)} />;
    }
  };
  hoistNonReactStatics(ComponentWithSentry, Component);
  return ComponentWithSentry;
};
