import React from 'react';
import { withResizeDetector } from 'react-resize-detector';
import { isSSR } from '@tipser/tipser-sdk';

import { ObjectFitImage } from '../lazy-img';
// it is needed to avoid unnecessary request when src is undefined
const placeholderImageStyle = { display: 'inline-block' };
const placeholderImage = <div style={placeholderImageStyle} />;

const defaultImgComponent = (props: { src: string }) => <ObjectFitImage {...props} />;

interface CloudinaryImgProps {
  cloudinaryId: string;
  imgComponent: React.ComponentType<{ src: string }>;
  width: number;
  height: number;
}

const BREAKPOINTS = {
  width: [250, 450, 960, 1560],
  height: [250, 450, 960], // big screens have higher width than height, so no need for the biggest height breakpoint
};

type Dimension = 'width' | 'height';

const getImageUrl = (id: string, dimensionValue: number, dimension: Dimension) =>
  `https://res.cloudinary.com/tipser/c_limit,f_auto,fl_lossy,q_auto,${dimension[0]}_${dimensionValue},d_no-image.png/img/${id}`;

const getImage = (id: string, smallerDimension: Dimension, dimensionValue: number) => {
  const breakpoints = BREAKPOINTS[smallerDimension];
  const suitableImageWidthCandidate = breakpoints.find((_) => _ > dimensionValue);
  const biggestImage = breakpoints[breakpoints.length - 1];
  const suitableImageWidth = suitableImageWidthCandidate ? suitableImageWidthCandidate : biggestImage;
  return getImageUrl(id, suitableImageWidth, smallerDimension);
};

function withResizeDetectorSafe(func) {
  let prevHeight = undefined;
  let prevWidth = undefined;
  return withResizeDetector(({ width, height, ...props }) => {
    prevHeight = height != null ? height : prevHeight;
    prevWidth = width != null ? width : prevWidth;
    return func({ ...props, width: prevWidth, height: prevHeight });
  });
}

const getDevicePixelRatio = () => {
  if (isSSR()) {
    return 1;
  }
  const devicePixelRatio = window.devicePixelRatio != null ? window.devicePixelRatio : 1;
  return Math.min(2, Math.max(1, devicePixelRatio));
};

const CloudinaryImgComponent = ({
  width,
  height,
  cloudinaryId,
  imgComponent = defaultImgComponent,
  ...props
}: CloudinaryImgProps) => {
  const devicePixelRatio = getDevicePixelRatio();
  const smallerDimension = width < height ? 'width' : 'height';
  const dimensionValue = Math.min(width, height) * devicePixelRatio;
  const src = width >= 0 ? getImage(cloudinaryId, smallerDimension, dimensionValue) : undefined;
  const ImgComponent = imgComponent;
  return src ? <ImgComponent {...props} src={src} /> : placeholderImage;
};

export const CloudinaryImg = withResizeDetectorSafe(CloudinaryImgComponent);
