import React, { ReactElement, ChangeEvent } from 'react';
import cx from 'classnames';

import { SmallArrowDown } from '../icons';

import './dropdown.less';

type Option = {
  label: string;
  value: string;
};

type Props = {
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
  options?: Option[];
  placeholder?: string;
  validate?: boolean;
};

export function Dropdown({ onChange, options, placeholder, validate }): ReactElement<Props> {
  return (
    <div className="te-dropdown-container">
      <div
        className={cx('te-dropdown-select', {
          'te-dropdown-validation-error': validate,
        })}
      >
        <select
          defaultValue="select-option"
          onChange={(e) => {
            onChange({ value: e.target.value, label: e.target.selectedOptions[0].innerText });
          }}
        >
          <option value="select-option" disabled>
            {placeholder}
          </option>
          {options.map((opt, index) => (
            <option key={index} value={opt.value}>
              {opt.label}
            </option>
          ))}
        </select>
        <SmallArrowDown />
      </div>
    </div>
  );
}
