import * as React from 'react';
import { InView } from 'react-intersection-observer';

const OBSERVER_DELAY = 1000;

export interface TrackableProps {
  setTrackingData?: (data: any) => void;
}

interface Options {
  trackingCallback: (data: any, props: any) => void;
}

export const withInViewTracker = (options: Options) => <K extends TrackableProps>(
  Component: React.ComponentType<K>
) => {
  const { trackingCallback } = options;

  return class extends React.Component<K> {
    timeoutRef = null;
    dataFromChild;

    setTrackingData = (trackingData) => (this.dataFromChild = trackingData);

    onChange = (inView: boolean) => {
      if (inView) {
        this.timeoutRef = setTimeout(() => trackingCallback(this.dataFromChild, this.props), OBSERVER_DELAY);
      } else if (this.timeoutRef !== null) {
        clearTimeout(this.timeoutRef);
        this.timeoutRef = null;
      }
    };

    render() {
      return (
        <InView as="div" onChange={this.onChange}>
          <Component {...this.props} setTrackingData={this.setTrackingData} />
        </InView>
      );
    }
  };
};
