import React, { ComponentType } from 'react';
import ReactResizeDetector from 'react-resize-detector';

import { ComponentModel } from '../../common/models';
import { fetchTipserCollection } from '../../api/tipser-api';
import { connectedElement, Contextable } from '../../tipser-context';
import { TipserProductComponent } from '../product/tipser-product';

import { TipserProductModel } from '../product/model';
import { customBemNamesFactory, BREAKPOINTS } from '../../common/utils';

import { Tracking, withInViewTracker, TrackableProps } from '../../common/analytics';
import { ImageSize } from '../product/product-card-renderer';
import { FadeAnimation } from '../fade-animation/fade-animation';

import './collection.less';
import './carousel.less';
import { PRODUCT_ALTERNATIVE_RENDERER } from '../product/product-renderer';
import { LoaderCenter } from '../../common/loader';
import { getCollectionFromServerState } from '../../state-builder';
import { CarouselArrow } from './carousel-arrow';

import Slider, { Settings } from 'react-slick';
import cx from 'classnames';

import { getBreakpoint, DEFAULT_SETTINGS, SETTINGS_MAP } from './slider-settings';
import './collection.less';
import { CancelablePromise, makeCancelable, silenceCancelable } from '../../common/utils/cancellable-promise';

const bem = customBemNamesFactory('te-collection');
const bemClasses = {
  xsmall: bem('xsmall'),
  small: bem('small'),
  large: bem('large'),
  extraLarge: bem('extra-large'),
};

export const COMPONENT_COLLECTION_CONTENT_TYPE = 'collection';

export interface CollectionType {
  // model of collection from tipser.com
  id: string;
  name?: string;
  created?: string;
  friendlyUrl?: string;
  items?: {
    // items are not productsById yet
    id?: string;
    product?: TipserProductModel; // here we have productsById
  }[];
}

export interface CollectionModel extends Partial<CollectionDefaultProps>, ComponentModel {
  products?: TipserProductModel[];
  collectionId?: string;
  collection?: CollectionType;
  title?: string;
  name?: string;
  posId?: string;
  carousel?: boolean;
}

interface CollectionProps extends TrackableProps, CollectionModel, Contextable {}

interface CollectionDefaultProps {
  imgSize: ImageSize;
  carousel: boolean;
}

export interface CollectionComponentState {
  collection?: CollectionType;
  loading: boolean;
  swipe: boolean;
  isComponentReady: boolean;
}

interface ConditionalSliderProps {
  children?: React.ReactNode;
  condition: boolean;
  sliderSettings: Settings;
  swipe: boolean;
  imgSize?: string;
}

// If you have a time you can try to type it properly, remove fragments and have fun
const ConditionalSlider: ComponentType<ConditionalSliderProps> = (props: ConditionalSliderProps) => {
  const isLarge = props.imgSize === 'large' || props.imgSize === '1.2';
  const slidesToShow = isLarge ? Math.max(props.sliderSettings.slidesToShow - 1, 1) : props.sliderSettings.slidesToShow;

  return props.condition ? (
    <Slider
      {...props.sliderSettings}
      slidesToShow={slidesToShow}
      swipe={props.swipe}
      nextArrow={<CarouselArrow type="next" slidesToShow={slidesToShow} />}
      prevArrow={<CarouselArrow type="prev" slidesToShow={slidesToShow} />}
    >
      {props.children}
    </Slider>
  ) : (
    <>{props.children}</>
  );
};

class CollectionNotConnected extends React.Component<CollectionProps, CollectionComponentState> {
  cancelableFetchCollection: CancelablePromise<CollectionType>;
  sliderSettings: Settings = {
    draggable: false,
    infinite: false,
    speed: 300,
    swipe: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: false,
    touchThreshold: 10,
    cssEase: 'ease-in-out',
  };

  static defaultProps: CollectionDefaultProps = {
    imgSize: 'medium',
    carousel: false,
  };

  constructor(props: CollectionProps) {
    super(props);
    this.state = {
      collection: getCollectionFromServerState(props.context, props.collectionId) || null,
      loading: false,
      swipe: true,
      isComponentReady: false,
    };
  }

  updateSliderSettings = (newSettings: Partial<Settings>) => {
    this.sliderSettings = {
      ...this.sliderSettings,
      ...newSettings,
    };
  };

  handleContainerWidthChange = (width: number) => {
    const { slidesToScroll } = this.sliderSettings;
    const breakpoint = getBreakpoint(width);
    const newSettings = SETTINGS_MAP[breakpoint] || DEFAULT_SETTINGS;

    if (newSettings.slidesToScroll !== slidesToScroll) {
      this.updateSliderSettings(newSettings);
    }
    if (!this.state.isComponentReady) {
      this.setState({
        isComponentReady: true,
      });
    }
  };

  componentDidMount() {
    const collectionFromServerState = getCollectionFromServerState(this.props.context, this.props.collectionId);
    if (this.props.collectionId && !collectionFromServerState) {
      this.loadCollectionData(this.props.collectionId);
    } else if (this.props.products) {
      this.loadCollectionFromProductList(this.props.products);
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.collectionId && prevProps.collectionId !== this.props.collectionId) {
      this.setState({
        loading: true,
      });
      this.loadCollectionData(this.props.collectionId);
    } else if (this.props.products && prevProps.products !== this.props.products) {
      this.loadCollectionFromProductList(this.props.products);
    }
  }

  loadCollectionFromProductList(products: TipserProductModel[]) {
    const items = products.map((item: TipserProductModel) => ({ product: item }));
    this.setData({
      items,
      id: undefined,
    });
  }

  loadCollectionData(collectionId) {
    const collectionFromServerState = getCollectionFromServerState(this.props.context, this.props.collectionId);
    if (!collectionFromServerState) {
      this.cancelableFetchCollection = makeCancelable(fetchTipserCollection(collectionId));

      silenceCancelable(this.cancelableFetchCollection.promise)
        .then((data) => this.setData(data))
        .catch(() => this.setState({ loading: false }));
    } else {
      this.setTrackingData(collectionFromServerState);
    }
  }

  setTrackingData(collection: CollectionType) {
    if (this.props.setTrackingData) {
      const { id = '(no id)', items } = collection;
      this.props.setTrackingData([{ id, items }]);
    }
  }

  setData(collection: CollectionType) {
    this.setState({ collection, loading: false });
    this.setTrackingData(collection);
  }

  getClassNameByWidth(width) {
    if (width < BREAKPOINTS.XSMALL) {
      return bemClasses.xsmall;
    } else if (width <= BREAKPOINTS.SMALL) {
      return bemClasses.small;
    } else if (width > BREAKPOINTS.SMALL && width < BREAKPOINTS.LARGE) {
      return bemClasses.large;
    } else {
      return bemClasses.extraLarge;
    }
  }

  disableCarouselWhenOpenModal = (swp) => {
    this.setState({
      swipe: !swp,
    });
  };

  componentWillUnmount(): void {
    this.cancelableFetchCollection && this.cancelableFetchCollection.cancel();
  }

  render() {
    const { carousel, imgSize } = this.props;
    const { collection, swipe, isComponentReady, loading } = this.state;
    const shouldRenderLoader = loading || (collection && !isComponentReady);
    return (
      <div className="te-collection-container">
        {collection && (
          <ReactResizeDetector handleWidth onResize={this.handleContainerWidthChange} refreshMode="debounce">
            {({ width }) => (
              <FadeAnimation
                className={cx('te-collection', this.getClassNameByWidth(width), { carousel })}
                fadeIn={isComponentReady}
                fadeOut={this.state.loading}
              >
                <ConditionalSlider
                  condition={this.props.carousel}
                  sliderSettings={this.sliderSettings}
                  swipe={swipe}
                  imgSize={imgSize}
                >
                  {isComponentReady &&
                    collection.items.map((item, index) => (
                      <TipserProductComponent
                        key={index}
                        dontLoadProduct={true}
                        product={item.product}
                        productId={item.product.id}
                        renderer={PRODUCT_ALTERNATIVE_RENDERER.CARD}
                        imgSize={this.props.imgSize}
                        disableCarouselWhenOpenModal={this.disableCarouselWhenOpenModal}
                      />
                    ))}
                </ConditionalSlider>
              </FadeAnimation>
            )}
          </ReactResizeDetector>
        )}

        {shouldRenderLoader && <LoaderCenter delayed />}
      </div>
    );
  }
}

export const Collection: ComponentType<CollectionProps> = connectedElement(CollectionNotConnected);

export const TrackableCollection: ComponentType<CollectionModel> = withInViewTracker({
  trackingCallback: (data) => data && Tracking.dispatchCollectionView(data),
})(Collection);
