import React from 'react';
import cx from 'classnames';

import { TipserProductVariantModel } from './model';
import { t } from '../../i18n';

export function UnitPrice({ variant, className }: { variant: TipserProductVariantModel; className?: string }) {
  const { unitPrice, unitPriceBaseMeasure } = variant;
  return (
    <div className={cx(className, 'te-font-small te-font-light')}>
      {unitPrice.formatted} / {unitPriceBaseMeasure.value} {t(`unit_price_${unitPriceBaseMeasure.unit}`)}
    </div>
  );
}
