import { CollectionType } from './components/collection/collection';
import { fetchTipserShop, fetchTipserProduct, fetchTipserCollection } from './api/tipser-api';
import { TipserProductModel } from './components/product/model';
import { TipserEnv } from '@tipser/tipser-sdk';
import { TipserContextData } from './tipser-context';

function keyById<T extends { id: string }>(list: Array<T>): { [id: string]: T } {
  return list.reduce((acc, item) => {
    acc[item.id] = item;
    return acc;
  }, {});
}

export interface TipserState {
  productsById: { [key: string]: TipserProductModel };
  collectionsById: { [key: string]: CollectionType };
  storeCollections: Array<CollectionType>;
}

export class StateBuilder {
  constructor(private posId: string, private env: TipserEnv = 'prod') {}

  private fetchStoreCollections(storeCollections?: Array<CollectionType>): Promise<{ [key: string]: CollectionType }> {
    if (!storeCollections) {
      return Promise.resolve({});
    }

    return Promise.all([storeCollections[0]].map((_) => fetchTipserCollection(_.id, this.env))).then((collections) =>
      keyById(collections)
    );
  }

  buildState(
    productIds: Array<string> = [],
    collectionIds: Array<string> = [],
    shouldFetchStore = false
  ): Promise<TipserState> {
    const productsPromise = Promise.all(
      productIds.map((id) => fetchTipserProduct(id, this.posId, this.env))
    ).then((products) => keyById(products));

    const collectionsPromise = Promise.all(
      collectionIds.map((id) => fetchTipserCollection(id, this.env))
    ).then((collections) => keyById(collections));

    const storeCollectionsPromise = shouldFetchStore
      ? fetchTipserShop(this.posId, this.env).then((response) => response.collections)
      : Promise.resolve<Array<CollectionType>>(undefined);
    return Promise.all([productsPromise, collectionsPromise, storeCollectionsPromise]).then(
      ([productsById, collectionsById, storeCollections]) =>
        this.fetchStoreCollections(storeCollections).then((storeCollectionsById) => ({
          productsById,
          collectionsById: { ...collectionsById, ...storeCollectionsById },
          storeCollections,
        }))
    );
  }
}

export function getCollectionFromServerState(context: TipserContextData, collectionId: string) {
  return context.initialState.collectionsById[collectionId];
}

export function getProductFromServerState(context: TipserContextData, productId: string) {
  return context.initialState.productsById[productId];
}

export function getStoreCollectionsFromServerState(context: TipserContextData) {
  return context.initialState.storeCollections;
}
