import { TipserLang } from '@tipser/tipser-sdk';

//TODO: requires some additional work in tsconfig to let us import json files
const fr = require('../../resources/fr.tipser-elements.json');
const de = require('../../resources/de.tipser-elements.json');
const en = require('../../resources/en.tipser-elements.json');
const sv = require('../../resources/sv.tipser-elements.json');
const fi = require('../../resources/fi.tipser-elements.json');
const dk = require('../../resources/da-DK.tipser-elements.json');

const translations = {
  fr,
  de,
  en,
  sv,
  fi,
  dk,
};

const configLangToLang: { [key in TipserLang]: I18nLang } = {
  [TipserLang.deDE]: 'de',
  [TipserLang.enUS]: 'en',
  [TipserLang.frFR]: 'fr',
  [TipserLang.svSE]: 'sv',
  [TipserLang.fiFI]: 'fi',
  [TipserLang.nlNL]: 'en',
  [TipserLang.daDK]: 'dk',
};

type I18nLang = 'en' | 'fr' | 'de' | 'sv' | 'fi' | 'dk';
const fallbackLang = 'en';

export const localeToLang = (configLang: TipserLang): I18nLang => {
  return configLangToLang[configLang];
};

let lang = fallbackLang;

export const setLang = (newLang: TipserLang): void => {
  lang = localeToLang(newLang);
};

export const t = (key, vars?) => {
  const bundle = translations[lang];
  const value = bundle[key] || translations[fallbackLang][key] || key;
  if (vars) {
    return render(value, vars);
  } else {
    return value;
  }
};

const render = (template, vars) => {
  return Object.keys(vars).reduce((acc, key) => acc.split(`{{${key}}}`).join(vars[key]), template);
};
