import React from 'react';
import cx from 'classnames';

import './button-clear.less';

export interface PropTypes extends React.ButtonHTMLAttributes<HTMLButtonElement> {}

const ButtonClear: React.FunctionComponent<PropTypes> = ({ className, ...restOfProps }) => {
  const finalClassName = cx('te-button-clear', className);
  return <button className={finalClassName} {...restOfProps} />;
};

export default ButtonClear;
