import React, { ComponentType, useEffect, useState } from 'react';
import { fetchLogo } from '../../api/tipser-api';
import { Tracking } from '../../common/analytics';
import { PrimaryButton } from '../../common/button-primary/button-primary';
import { CloudinaryImg } from '../../common/cloudinary-img';
import { Modal } from '../../common/modal/modal';
import { customBemNamesFactory } from '../../common/utils';
import { t } from '../../i18n';
import { connectedElement, Contextable } from '../../tipser-context';
import Footer from './Footer';
import './footer.less';
import Header from './Header';
import './header.less';
import { TipserProductModel, TipserProductVariantModel } from './model';
import './product-card.less';
import { TipserProductComponent } from './tipser-product';
import { UnitPrice } from './unit-price';
import { makeCancelable, silenceCancelable } from '../../common/utils/cancellable-promise';

const bem = customBemNamesFactory('te-product-card');

export type ImageSize = 'small' | 'medium' | 'large' | '0.8' | '1.0' | '1.2';

interface ProductCardRendererProps extends Contextable {
  selectedVariant?: TipserProductVariantModel;
  product: TipserProductModel;
  posId: string;
  imgSize: ImageSize;
  viewMode: string;
  onProductClick?: (product: TipserProductModel) => void;
  disableCarouselWhenOpenModal?: (bool) => any;
}

class ProductCardRendererNotConnected extends React.Component<ProductCardRendererProps> {
  state = {
    modalVisible: false,
  };

  toggleModal = () => {
    if (this.props.context.openOldDialog) {
      this.props.context.tipserSdk.openProductDialog(this.props.product.id);
    } else {
      const { product } = this.props;
      if (!this.state.modalVisible) {
        Tracking.dispatchClickProduct(product);
        Tracking.dispatchViewProductDetails(product);
      }

      this.setState(
        {
          modalVisible: !this.state.modalVisible,
        },
        this.updateScrollingPolicy
      );
    }
  };

  onProductClick = () => {
    if (this.props.context.disableDialog) {
      window.location.href = this.props.context.tipserSdk.getProductDialogUrl(this.props.product.id);
    } else {
      this.toggleModal();
    }
    if (this.props.onProductClick) {
      this.props.onProductClick(this.props.product);
    }
  };

  getProductImage() {
    const image = this.props.product.images[0];
    return image && image['id'];
  }

  getBuyButtonLabel() {
    const customLabels = this.props.context.customLabels;
    const getLabel = (key: string) => (customLabels[key] ? customLabels[key] : t(key));
    if (this.props.product.isInStock) {
      return getLabel('buy');
    }
    return getLabel('unavailable');
  }

  updateScrollingPolicy = () => {
    const body = document.body;
    const { disableCarouselWhenOpenModal } = this.props;

    if (this.state.modalVisible) {
      body.classList.add('no-scroll');
      if (disableCarouselWhenOpenModal) {
        disableCarouselWhenOpenModal(true);
      }
    } else {
      body.classList.remove('no-scroll');

      if (disableCarouselWhenOpenModal) {
        disableCarouselWhenOpenModal(false);
      }
    }
  };

  render() {
    const { product, posId, imgSize, viewMode, selectedVariant } = this.props;

    return (
      <React.Fragment>
        {product && (
          <div
            onClick={this.onProductClick}
            className={bem({
              small: imgSize == 'small' || imgSize == '0.8',
              large: imgSize == 'large' || imgSize == '1.2',
              compact: viewMode === 'compact',
            })}
          >
            <CloudinaryImg cloudinaryId={this.getProductImage()} className={'te-product-card-img'} />
            <div className="te-product-card-details">
              <div className="te-product-card-brand">{product.brand}</div>
              <div className="te-product-card-name">{product.title}</div>

              {product.discountPriceIncVat ? (
                <div className="te-product-card-price">
                  {<div className="te-product-card-sales-price">{product.discountPriceIncVat.formatted}</div>}
                  {<div className="te-product-card-regular-price">{product.priceIncVat.formatted}</div>}
                </div>
              ) : (
                <div className="te-product-card-price">{product.priceIncVat.formatted}</div>
              )}

              {selectedVariant && selectedVariant.unitPrice && (
                <UnitPrice className="te-product-card-unit-price" variant={selectedVariant} />
              )}

              <PrimaryButton disabled={!product.isInStock}>{this.getBuyButtonLabel()}</PrimaryButton>
            </div>
          </div>
        )}

        {!this.props.context.openOldDialog && this.state.modalVisible && (
          <Modal onClose={this.toggleModal}>
            <ModalContainer posId={posId}>
              <div className="te-tipser-product-wrapper">
                <TipserProductComponent productId={product.id} posId={posId} useInDialog={true} />
              </div>
            </ModalContainer>
          </Modal>
        )}
      </React.Fragment>
    );
  }
}

const useLogo = (posId: string) => {
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    const { cancel, promise } = makeCancelable(fetchLogo(posId));
    silenceCancelable(promise).then(setLogo);
    return cancel;
  }, [posId]);
  return logo;
};

const ModalContainer = ({ posId, children }: { posId: string; children: any }) => {
  const logo = useLogo(posId);

  return (
    <>
      <Header logo={logo} />
      {children}
      <Footer logo={logo} />
    </>
  );
};
export const ProductCardRenderer: ComponentType<ProductCardRendererProps> = connectedElement(
  ProductCardRendererNotConnected
);
