import React from 'react';
import { withButtonStyle } from '../../components/link-button/with-button-style';
import cx from 'classnames';

interface PrimaryButtonProps {
  className?: string;
  onClick?: () => void;
  children: string;
  disabled?: boolean;
}

export class PrimaryButtonStyleless extends React.Component<PrimaryButtonProps> {
  render() {
    const { className } = this.props;
    const disabled = this.props.disabled || false;
    const finalClassNames = cx(className, { disabled: disabled });
    const bodyClassNames = cx('te-button-body', { 'te-primary-fill': !disabled });
    return (
      <div className={finalClassNames} onClick={this.props.onClick}>
        <div className={bodyClassNames} />
        <span className="te-button-text">{this.props.children}</span>
      </div>
    );
  }
}

export const PrimaryButton = withButtonStyle(PrimaryButtonStyleless, 'PrimaryButton');
