import { TipserEnv } from '@tipser/tipser-sdk';

import { TipserContextData, getTipserContextData } from '../tipser-context';
import { t } from '../i18n';
import { CollectionType } from '../components/collection/collection';
import { TipserProductModel } from '../components/product/model';
import { getTipserApiUrl } from '../common/tipser-urls';

const SERVER_ERROR_MESSAGE = t('default_server_error');

const getUrl = (env?: TipserEnv) => {
  const context: TipserContextData = getTipserContextData();
  if (context) {
    return context.tipserApiUrl;
  }
  if (env) {
    return getTipserApiUrl(env);
  }
  console.warn('Env is not defined, falling back to prod');
  return getTipserApiUrl(TipserEnv.prod);
};

const get = <T>(url: string): Promise<T> =>
  fetch(url).then((response) => {
    if (response.ok) {
      return response.json();
    }
    if (response.status >= 500) {
      console.error(SERVER_ERROR_MESSAGE, response);
    }
    return Promise.reject(response);
  });

export const fetchTipserProduct = (productId: string, posId: string, env?: TipserEnv): Promise<TipserProductModel> => {
  const url = getUrl(env);
  const getProductUrl = `${url}/v4/products/${productId}?pos=${posId}`;
  return get<TipserProductModel>(getProductUrl);
};

export const fetchTipserCollection = (collectionId: string, env?: TipserEnv): Promise<CollectionType> => {
  const url = getUrl(env);
  return get<CollectionType>(`${url}/v4/collections/${collectionId}`);
};

export const fetchTipserShop = (posId: string, env?: TipserEnv): Promise<{ collections: Array<CollectionType> }> => {
  const url = getUrl(env);
  return get<{ collections: Array<CollectionType> }>(`${url}/v4/shops/byId/${posId}`);
};

export const fetchLogo = (posId: string, env?: TipserEnv): Promise<string> => {
  const url = getUrl(env);
  return get<{ profilePictureUrl: { '250x': string } }>(`${url}/v3/profile/byId/${posId}`).then(
    (_) => _.profilePictureUrl['250x']
  );
};
