import React from 'react';
import cx from 'classnames';

import { TipserProductModel, TipserProductVariantModel } from './model';
import { UnitPrice } from './unit-price';
import { t } from '../../i18n';

export default function ProductBasicInfo({
  className,
  product,
  selectedVariant,
}: {
  className?: string;
  product: TipserProductModel;
  selectedVariant?: TipserProductVariantModel;
}) {
  const hasDiscount = product.discountPriceIncVat || (selectedVariant && selectedVariant.discountPriceIncVat);
  return (
    <div className={cx(className, 'te-product-basic-info')}>
      <div className="te-product-name">{product.title} </div>
      <div className="te-product-brand te-primary-color">{product.brand}</div>

      <div className="te-product-price-container">
        <div className="te-product-price-main">
          {hasDiscount && (
            <div className="te-product-price te-product-sales-price">
              {selectedVariant && selectedVariant.discountPriceIncVat
                ? selectedVariant.discountPriceIncVat.formatted
                : product.discountPriceIncVat.formatted}
            </div>
          )}

          <div
            className={cx('te-product-price', {
              'te-product-regular-price': hasDiscount,
            })}
          >
            {selectedVariant ? selectedVariant.priceIncVat.formatted : product.priceIncVat.formatted}
          </div>

          {selectedVariant && selectedVariant.unitPrice && (
            <UnitPrice className="te-product-unit-price" variant={selectedVariant} />
          )}
        </div>
        <div className="te-product-price-vat-info">
          {t('incl_vat', { vat: product.vat ? product.vat.formatted : '' })}
        </div>
      </div>
    </div>
  );
}
