import React, { useState, useEffect, ComponentType } from 'react';

import { fetchTipserProduct } from '../../api/tipser-api';
import { TrackableProps, Tracking, withInViewTracker } from '../../common/analytics';
import { connectedElement, Contextable } from '../../tipser-context';
import { Collection } from '../../';

export interface ProductListProps extends Contextable, TrackableProps {
  productIds: Array<string>;
  carousel?: boolean;
}

export const TrackableProductListNotConnected = ({ productIds, context, carousel }: ProductListProps) => {
  const { posId } = context;

  const [products, setProducts] = useState([]);
  useEffect(() => {
    Promise.all(productIds.map((id) => fetchTipserProduct(id, posId))).then(setProducts);
  }, [productIds, posId]);

  return <Collection products={products} carousel={carousel} />;
};

export const ProductList: ComponentType<ProductListProps> = connectedElement(TrackableProductListNotConnected);

export const TrackableProductList: ComponentType<ProductListProps> = withInViewTracker({
  trackingCallback: (data) => data && Tracking.dispatchCollectionView(data),
})(ProductList);
