import { Actions } from './actions';
import { Targets } from './targets';

export class TrackingPayload {
  constructor(
    public description: string,
    public action: Actions,
    public target: Targets,
    public object: Array<any>,
    public extraAttrs: any = {}
  ) {}

  static fromTipserSdkIframeObject(data: Partial<TrackingPayload>) {
    return new TrackingPayload(data.description, data.action, data.target, data.object, data.extraAttrs);
  }
}
