import React, { PureComponent } from 'react';
import cx from 'classnames';

import './fade-animation.less';

export interface FadeAnimationProps {
  className?: string;
  fadeIn?: boolean;
  fadeOut?: boolean;
  delayed?: boolean;
  children: React.ReactNode;
}

export class FadeAnimation extends PureComponent<FadeAnimationProps> {
  render() {
    const { fadeIn, fadeOut, children, className, delayed } = this.props;

    const classNames = cx(className, {
      'fade-in': fadeIn && !delayed,
      'delayed-fade-in': fadeIn && delayed,
      'fade-out': fadeOut,
    });

    return <div className={classNames}>{children}</div>;
  }
}
