import React from 'react';
import ReactDOM from 'react-dom';
import { isSSR } from '@tipser/tipser-sdk';

import '../../../../library/src/components/collection/carousel.less';
import './modal.less';
import './inner-image-zoom.less';
import ButtonCross from '../button-cross/button-cross';
import { Cart } from '../../shopping/cart-icon';

interface ModalProps {
  onClose?: () => void;
  posId?: string;
}

export class Modal extends React.Component<ModalProps, any> {
  clickEventListener;

  componentDidMount() {
    this.clickEventListener = document.body.addEventListener('keydown', this.onKeyDown);
    document.body.classList.add('no-scroll');
  }

  componentWillUnmount() {
    window.document.body.removeEventListener('keydown', this.onKeyDown);
  }

  onKeyDown = (e) => {
    const { onClose } = this.props;
    const isEscapeKey = e.key === 'Escape' || e.key === 'Esc'; //'Esc' happens on IE11
    if (isEscapeKey && onClose) {
      onClose();
      document.body.classList.remove('no-scroll');
    }
  };

  render() {
    if (isSSR()) {
      return null;
    }
    const { children, onClose } = this.props;

    return ReactDOM.createPortal(
      <div className="te-product-overlay">
        <div className="te-product-card-modal">
          <div className="te-product-card-modal-cart">
            <Cart onCheckoutClick={onClose} />
            <ButtonCross onClick={onClose} />
          </div>
          <div className="te-product-card-modal-inner">{children}</div>
        </div>
      </div>,
      document.querySelector('body')
    );
  }
}
