import { Subscriber } from '.';
import { getSdkInstance } from '../../../tipser-context';
import { TrackingPayload } from '../tracking-payload';
import { GaEventPayload, toGaLocation } from '@tipser/tipser-sdk';
import { ProductModel } from '../../../ecommerce';
import { Actions } from '../actions';
import { Targets } from '../targets';

const createBaseEvent = () => ({
  hitType: 'event',
  location: toGaLocation(window.location.href),
});

const mapProductClickedEventsToGa = (product: ProductModel): GaEventPayload => ({
  eventPayload: {
    ...createBaseEvent(),
    eventCategory: 'E-commerce',
    eventAction: 'click',
  },
  ecData: [{ command: 'addProduct', payload: { id: product.id } }],
});

const mapProductViewedEventsToGa = (eventAction: string) => (product: ProductModel): GaEventPayload => ({
  eventPayload: {
    ...createBaseEvent(),
    eventCategory: 'E-commerce',
    eventAction,
    nonInteraction: true,
  },
  ecData: [{ command: 'addImpression', payload: { id: product.id } }],
});

const mapProductViewedToGa = mapProductViewedEventsToGa('impression');
const mapProductDetailsViewedToGa = mapProductViewedEventsToGa('details');

const mapShopViewedToGa = ({ items }: { items: Array<ProductModel> }) => ({
  eventPayload: {
    ...createBaseEvent(),
    eventCategory: 'Shops',
    eventAction: 'shop-viewed',
    nonInteraction: true,
  },
  ecData: items.map((item) => ({ command: 'addImpression', payload: { id: item.id } })),
});

const mapCollectionViewedToGa = ({ items }: { items: Array<{ product: ProductModel }> }) => ({
  eventPayload: {
    ...createBaseEvent(),
    eventCategory: 'Shops',
    eventAction: 'collection-viewed',
    nonInteraction: true,
  },
  ecData: items.map((item) => ({ command: 'addImpression', payload: { id: item.product.id } })),
});

const joinActionAndTarget = (action: Actions, target: Targets) => `${action}|${target}`;

const PRODUCT_DETAILS_VIEW = joinActionAndTarget(Actions.VIEW, Targets.PRODUCT);
const PRODUCT_TILE_VIEW = joinActionAndTarget(Actions.VIEW, Targets.PRODUCT_TILE);
const COLLECTION_VIEW = joinActionAndTarget(Actions.VIEW, Targets.COLLECTION);
const SHOP_VIEW = joinActionAndTarget(Actions.VIEW, Targets.SHOP);
const PRODUCT_CLICK = joinActionAndTarget(Actions.CLICK, Targets.PRODUCT_TILE);

export class GaSubscriber implements Subscriber {
  mapPayloadToGa(payload: TrackingPayload): GaEventPayload {
    const actionAndTarget = joinActionAndTarget(payload.action, payload.target);

    switch (actionAndTarget) {
      case PRODUCT_TILE_VIEW:
        return mapProductViewedToGa(payload.object[0]);
      case PRODUCT_DETAILS_VIEW:
        return mapProductDetailsViewedToGa(payload.object[0]);
      case COLLECTION_VIEW:
        return mapCollectionViewedToGa(payload.object[0]);
      case SHOP_VIEW:
        return mapShopViewedToGa(payload.object[0]);
      case PRODUCT_CLICK:
        return mapProductClickedEventsToGa(payload.object[0]);
      default:
        return null;
    }
  }

  sendEvent(detail: TrackingPayload) {
    const sdkInstance = getSdkInstance();
    if (!sdkInstance) {
      return;
    }
    const payload = this.mapPayloadToGa(detail);
    payload && sdkInstance.analytics.sendGaEvent(payload);
  }
}
