import React, { ComponentType } from 'react';
import ReactResizeDetector from 'react-resize-detector';
import cx from 'classnames';
import { isSSR } from '@tipser/tipser-sdk';

import { connectedElement, Contextable } from '../../tipser-context';
import { fetchTipserShop } from '../../api/tipser-api';
import { Collection, CollectionType } from '../collection/collection';
import { customBemNamesFactory, BREAKPOINTS } from '../../common/utils';
import { ComponentModel } from '../../common/models';
import { withSentry } from '../../common/sentry/with-sentry';
import { TrackableProps, Tracking, withInViewTracker } from '../../common/analytics';
import { ProductModel } from '../../ecommerce';
import { SmallArrowDown } from '../../common/icons';

import './store.less';
import { getStoreCollectionsFromServerState } from '../../state-builder';

const bem = customBemNamesFactory('te-store');
const bemClasses = {
  small: bem('small'),
  large: bem('large'),
  desktopMenu: bem('desktop-menu'),
  mobileMenu: bem('mobile-menu'),
  collection: bem('collection'),
};

export const COMPONENT_STORE_CONTENT_TYPE = 'store';
const COLLECTION_ID_PATTERN = /[0-9a-f]{24}/g;

export interface StoreModel extends ComponentModel {
  posId?: string;
}

export type StoreComponentProps = StoreModel & Contextable & TrackableProps;

export interface StoreState {
  activeCollection: CollectionType;
  collections: CollectionType[];
}

class StoreNotConnected extends React.Component<StoreComponentProps, StoreState> {
  constructor(props: StoreComponentProps) {
    super(props);
    const collectionsFromServerState = getStoreCollectionsFromServerState(props.context);
    this.state = {
      collections: collectionsFromServerState || [],
      activeCollection: collectionsFromServerState ? this.getActiveCollection(collectionsFromServerState) : null,
    };
  }

  wasCollectionChangedByClick = false;

  componentDidMount() {
    const collectionsFromServerState = getStoreCollectionsFromServerState(this.props.context);
    if (collectionsFromServerState) {
      return;
    }
    const posId = this.props.posId || this.props.context.posId;

    fetchTipserShop(posId).then((response) => {
      const collections = response.collections;

      if (collections && collections.length > 0) {
        const activeCollection = this.getActiveCollection(collections);
        this.setState({
          collections,
          activeCollection,
        });
      }
    });
  }

  collectionClicked(collectionId: string) {
    this.wasCollectionChangedByClick = true;
    const activeCollection = this.state.collections.find((collection) => collection.id === collectionId);
    this.setState({ activeCollection });
    this.saveCollectionIdToUrl(activeCollection.id);
  }

  onTrackingDataChange = ([data]: any) => {
    this.props.setTrackingData && this.props.setTrackingData(data);
    if (this.wasCollectionChangedByClick) {
      Tracking.dispatchShopView([
        {
          ...getTrackingPayload(),
          ...data,
        },
      ]);
    }
  };

  getActiveCollection = (collections: Array<CollectionType>) => {
    const collectionIdFromUrl = this.readCollectionIdFromUrl();
    const collectionFromUrl =
      collectionIdFromUrl && collections.find((collection) => collection.id === collectionIdFromUrl);
    return collectionFromUrl || collections[0];
  };

  saveCollectionIdToUrl(collectionId: string) {
    if (this.props.context.useDeepLinking) {
      window.location.hash = collectionId;
    }
  }

  readCollectionIdFromUrl() {
    if (isSSR() || !this.props.context.useDeepLinking) {
      return null;
    }
    const collectionIdFromHash = window.location.hash ? window.location.hash.substr(1) : null;
    if (collectionIdFromHash && collectionIdFromHash.match(COLLECTION_ID_PATTERN)) {
      return collectionIdFromHash;
    }
    return null;
  }

  getClassNameByWidth(width) {
    return width <= BREAKPOINTS.SMALL ? bemClasses.small : bemClasses.large;
  }

  render() {
    return (
      <ReactResizeDetector handleWidth>
        {({ width }) => (
          <div className={`te-store ${this.getClassNameByWidth(width)}`}>
            {width <= BREAKPOINTS.SMALL ? this.renderMenuForMobile() : this.renderMenuForDesktop()}

            <div className={bemClasses.collection}>
              {this.state.activeCollection && (
                <Collection setTrackingData={this.onTrackingDataChange} collectionId={this.state.activeCollection.id} />
              )}
            </div>
          </div>
        )}
      </ReactResizeDetector>
    );
  }

  private renderMenuForMobile() {
    return (
      <div className={bemClasses.mobileMenu}>
        <select
          onChange={(e) => {
            this.collectionClicked(e.target.value);
          }}
        >
          {this.state.collections.map((collection, index) => {
            return (
              <option key={index} value={collection.id}>
                {collection.name}
              </option>
            );
          })}
        </select>
        <SmallArrowDown />
      </div>
    );
  }

  private renderMenuForDesktop() {
    return (
      <ul className={bemClasses.desktopMenu}>
        {this.state.collections.map((collection) => {
          const activeClassName = collection.id === this.state.activeCollection.id ? 'te-primary-border' : '';
          return (
            <li
              key={collection.id}
              onClick={() => this.collectionClicked(collection.id)}
              className={cx('te-primary-border-hover', activeClassName, bem('menu-item'))}
            >
              {collection.name}
            </li>
          );
        })}
      </ul>
    );
  }
}

const StoreComponent: ComponentType<StoreComponentProps> = connectedElement(StoreNotConnected);

const getTrackingPayload = (): { url_slug: string } => {
  const { host, pathname } = window.location;
  const url_slug = host + pathname;
  return { url_slug };
};

export const Store: ComponentType<StoreComponentProps> = withSentry(
  withInViewTracker({
    trackingCallback: (data: { items: Array<ProductModel>; id: string }) =>
      data &&
      Tracking.dispatchShopView([
        {
          ...getTrackingPayload(),
          ...data,
        },
      ]),
  })(StoreComponent)
);
