import * as Sentry from '@sentry/browser';
import { EventHint, Event } from '@sentry/types';
import { isSSR } from '@tipser/tipser-sdk';

const SENTRY_DSN = 'https://010d69b2659449ca8607d57a067ab5cb@sentry.io/1513228';
class SentryClientWrapper {
  isInitialized = false;

  private isTipserError(hint?: EventHint): boolean {
    try {
      return (
        hint &&
        hint.originalException &&
        hint.originalException.hasOwnProperty &&
        hint.originalException.hasOwnProperty('isTipserError') &&
        hint.originalException['isTipserError']
      );
    } catch (_e) {
      return false;
    }
  }

  initialize() {
    if (isSSR()) {
      return;
    }
    const host = (location && location.host) || '';
    const isLocalhost = host.startsWith('localhost');

    if (!this.isInitialized && !isLocalhost) {
      this.isInitialized = true;
      Sentry.init({
        dsn: SENTRY_DSN,
        beforeSend: (event: Event, hint?: EventHint) => {
          if (this.isTipserError(hint)) {
            return event;
          }
          return null;
        },
      });
    }
  }
}

export const SentryClient = new SentryClientWrapper();
