import { isSSR, TipserEnv, TipserSDKInstance } from '@tipser/tipser-sdk';
import { TipserConfigEventHandlers } from '../config';
import { fetchTipserProduct } from '../api/tipser-api';
import { TipserProductModel } from '../components/product/model';

const ANALYTICS_EVENT_COMMAND = 'tipser.widget.log';
const CART_ACTION = 'Cart';

let initialized = false;

export const initAddToCartPostMessageHandler = (
  events: TipserConfigEventHandlers,
  tipserSdk: TipserSDKInstance,
  posId: string,
  env: TipserEnv
) => {
  if (!events.onAddToCart || initialized || isSSR()) {
    return;
  }

  window.addEventListener('message', (message) => {
    if (!message.data) {
      return;
    }

    const data = message.data;
    if (events.onAddToCart && data.command === ANALYTICS_EVENT_COMMAND && data.trackData.action === CART_ACTION) {
      const productId = data.trackData.object[0].id;

      //We need to fetch product data to call the callback with a correct product model (more complete than the analytics model)
      fetchTipserProduct(productId, posId, env).then((product: TipserProductModel) => {
        tipserSdk.getCurrentCartSize().then((cartSize: number) =>
          events.onAddToCart({
            product,
            cartSize,
          })
        );
      });
    }
  });

  initialized = true;
};
