import React, { useCallback, useEffect, useState } from 'react';

import { AddedToCartPopupDumb } from './added-to-cart-popup-dumb';
import { connectedElement, EVENT_ADD_TO_CART } from '../../core';
import { TipserProductModel } from '../product/model';
import { Contextable } from '../../tipser-context';
import { Popup } from '../popup';

interface AddedToCartPopupNotConnectedProps extends Contextable {
  onCheckoutClick?: () => void;
}

function AddedToCartPopupNotConnected({
  context,
  onCheckoutClick: onCheckoutClickCallback,
}: AddedToCartPopupNotConnectedProps) {
  const [isVisible, setVisibility] = useState(false);
  const [product, setProduct] = useState<TipserProductModel>(undefined);

  const onAddToCart = useCallback(
    ({ product }: { product: TipserProductModel }) => {
      setProduct(product);
      setVisibility(true);
    },
    [setProduct, setVisibility]
  );

  useEffect(() => {
    context.events.register(EVENT_ADD_TO_CART, onAddToCart);
    return () => {
      context.events.unregister(EVENT_ADD_TO_CART, onAddToCart);
    };
  }, [context]);

  const onCheckoutClick = useCallback(() => {
    if (context.disableDialog) {
      window.location.href = context.tipserSdk.getPurchaseDialogUrl();
    } else {
      context.tipserSdk.openPurchaseDialog();
    }
    onCheckoutClickCallback && onCheckoutClickCallback();
  }, [context]);

  const onClose = useCallback(() => {
    setVisibility(false);
    setProduct(undefined);
  }, [setVisibility, setProduct]);

  return (
    <Popup isVisible={isVisible} onClose={onClose} name="added-to-cart-popup">
      <AddedToCartPopupDumb onClose={onClose} onCheckoutClick={onCheckoutClick} product={product} />
    </Popup>
  );
}

export const AddedToCartPopup = connectedElement(AddedToCartPopupNotConnected);
