import React, { useEffect, useMemo, FC } from 'react';
import ReactDOM from 'react-dom';
import { Transition } from 'react-transition-group/esm';
import { noop } from '../../common/utils';

import { withoutSSR } from '../../common/utils/without-ssr';

import './popup.less';
import { usePrevious } from '../../common/hooks/use-previous';

const APPEARANCE_DURATION = 5_000;

export interface PopupProps {
  isVisible: boolean;
  onClose: () => void;
  name: string;
}

const POPUP_ID_ATTR_NAME = 'data-popup-id';
const POPUP_CONTAINER_ELEMENT_TYPE = 'div';

const getContainerSelector = (name: string) => `${POPUP_CONTAINER_ELEMENT_TYPE}[${POPUP_ID_ATTR_NAME}=${name}]`;
const getDomElement = (name: string) => document.body.querySelector(getContainerSelector(name));
const isElementInDOM = (name: string) => getDomElement(name) != null;

function useContainer(name: string) {
  const prevName = usePrevious(name);
  const element = useMemo(() => {
    if (isElementInDOM(name)) {
      return null;
    }
    if (prevName && prevName !== name && isElementInDOM(prevName)) {
      document.removeChild(getDomElement(prevName));
    }
    const newElement = document.createElement(POPUP_CONTAINER_ELEMENT_TYPE);
    newElement.setAttribute(POPUP_ID_ATTR_NAME, name);
    return newElement;
  }, [name, prevName]);

  useEffect(() => {
    if (!element) {
      return noop;
    }
    document.body.appendChild(element);
    return () => {
      document.body.removeChild(element);
    };
  }, [element]);
  return element;
}

const PopupBase: FC<PopupProps> = ({ isVisible, onClose, children, name }) => {
  const container = useContainer(name);

  if (!container) {
    return null;
  }
  return ReactDOM.createPortal(
    <Transition
      in={isVisible}
      onEntered={onClose}
      timeout={APPEARANCE_DURATION}
      unmountOnExit
      mountOnEnter
      exit={false}
    >
      {children}
    </Transition>,
    container
  );
};

export const Popup = withoutSSR(PopupBase);
