import { SiteModel } from './components/site/site';
import { TipserSdkConfig, TipserSDKInstance } from '@tipser/tipser-sdk';
import { EventBus } from './tipser-events';
import { TipserProductModel } from './components/product/model';
import { TipserState } from './state-builder';
import { Pushable } from './tipser-context';

/**
 * This configuration interface should reflect publicly documented configuration.
 * Non optional properties, are eventually set by default values
 */
export interface TipserElementsConfig extends TipserSdkConfig {
  defaultAddedToCartPopup: boolean;
  useDefaultErrorHandler: boolean;
  eventsHandlers?: TipserConfigEventHandlers;
  useDeepLinking: boolean;
  customLabels: { [key in 'buy' | 'unavailable']?: string };
  isWidget: boolean;
  openOldDialog: boolean;
  disableDialog: boolean;
  addToCartSettings?: {
    directToCheckoutMode?: boolean;
  };
}

/**
 * Defines everything that is inside of Tipser's React context:
 * - config
 *   - consists of both tipser-elements and tipser-sdk properties
 * - other context properties like, necessary for communication between core components
 *    - e.g. sdk instance, Contentful api keys
 *
 * TODO: consider removing optional mod from properties here, as they are most probably all set by default values
 */
export interface TipserCommonConfigContextData extends TipserElementsConfig {
  posId: string;
  gaTrackingCode?: string;
  ga?: string;
  contentfulApiKey?: string;
  contentfulSpace?: string;
  tipserApiUrl?: string;
  contentfulEnv?: string;
  isGaEnabled?: boolean;
  tipserSdk?: TipserSDKInstance;
  cms?: object;
  site?: SiteModel;
  isEmbeddedInShop?: boolean;
  disableDialog: boolean;
}

export interface TipserConfigEventHandlers {
  onAddToCart?: (params: { cartSize?: number; product?: TipserProductModel }) => void;
  onError?: () => void;
}

export interface TipserContextData extends TipserCommonConfigContextData {
  initialState?: TipserState;
  events?: EventBus;
  history?: Pushable;
}

const DEFAULT_CONTENTFUL_API_KEY =
  process.env.CONTENTFUL_API_KEY || 'e969dc3e3878fd07771d8d3fe3a662f56bff942f7c37277d894dbcd241679fe7';
const DEFAULT_CONTENTFUL_SPACE = process.env.CONTENTFUL_SPACE || 'i8t5uby4h6ds';
const DEFAULT_CONTENTFUL_ENV = process.env.CONTENTFUL_ENV || 'master';
const DEFAULT_IS_GA_ENABLED = process.env.IS_GA_ENABLED || false;
const DEFAULT_IS_WIDGET = process.env.IS_WIDGET || false;
const DEFAULT_GA_TRACKING_CODE = 'UA-26837765-10';
const DEFAULT_CMS_CONNECTOR_CONFIG = {
  host: 'cdn.contentful.com',
};
const DEFAULT_ADD_TO_CART_SETTINGS = {
  directToCheckoutMode: false,
};
const DEFAULT_DEFAULT_ADDED_TO_CART_POPUP = true;
const DEFAULT_USE_DEEP_LINKING = true;

// Following default values most probably should not override those from TipserSDK
const defaultConfigValues = {
  defaultAddedToCartPopup: DEFAULT_DEFAULT_ADDED_TO_CART_POPUP,
  useDefaultErrorHandler: DEFAULT_USE_DEEP_LINKING,
  eventsHandlers: undefined,
  useDeepLinking: true,
  customLabels: {},
  isWidget: !!DEFAULT_IS_WIDGET,
  openOldDialog: true,
  disableDialog: false,
};

export type DefaultConfigValues = typeof defaultConfigValues;

const EMPTY_INITIAL_STATE = {
  productsById: {},
  collectionsById: {},
  storeCollections: undefined,
};

const defaultTipserCommonConfigContext = {
  cms: DEFAULT_CMS_CONNECTOR_CONFIG,
  gaTrackingCode: DEFAULT_GA_TRACKING_CODE,
  // TODO : as we have cms config option, maybe we can move contentful options there
  contentfulApiKey: DEFAULT_CONTENTFUL_API_KEY,
  contentfulSpace: DEFAULT_CONTENTFUL_SPACE,
  site: null,
  contentfulEnv: DEFAULT_CONTENTFUL_ENV,
  isGaEnabled: !!DEFAULT_IS_GA_ENABLED,
  addToCartSettings: DEFAULT_ADD_TO_CART_SETTINGS,
  initialState: EMPTY_INITIAL_STATE,
  redirectToProductMode: false,
};

export function addDefaultValuesToConfig(config: Partial<TipserElementsConfig>): TipserElementsConfig {
  const newConfig = { ...config };
  Object.keys(defaultConfigValues).forEach((configKey) => {
    if (!(configKey in newConfig)) {
      newConfig[configKey] = defaultConfigValues[configKey];
    }
  });
  return newConfig as TipserElementsConfig;
}

export function addDefaultContextData(
  config: TipserElementsConfig,
  props: { posId: string; initialState?: TipserState; history?: Pushable },
  eventBus: EventBus
): TipserContextData {
  const explicitlyAddedProps = ['posId', 'initialState', 'history'].reduce(
    (acc, current) => (current in props ? { ...acc, [current]: props[current] } : acc),
    {}
  ) as { posId: string; initialState?: TipserState; history?: Pushable };

  return { ...defaultTipserCommonConfigContext, ...config, ...explicitlyAddedProps, events: eventBus };
}
