import React, { ComponentType } from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';
import { isSSR } from '@tipser/tipser-sdk';

import { getDisplayName } from '../utils';

export function withoutSSR<T>(Component: ComponentType<T>): ComponentType<T> {
  const ComponentWithoutSSR = (props: T) => (isSSR() ? null : <Component {...props} />);
  ComponentWithoutSSR.displayName = `withoutSSR(${getDisplayName(Component)})`;
  hoistNonReactStatics(ComponentWithoutSSR, Component);
  return ComponentWithoutSSR;
}
