import React from 'react';
import cx from 'classnames';
import marked from 'marked';
import { t } from '../../i18n';

interface DescriptionRenderProps {
  showMoreButton: boolean;
  showMoreClicked: () => void;
  description: string;
  descriptionHeight: number | string;
  descriptionRef: React.Ref<HTMLDivElement>;
  descriptionMore: boolean;
}

export const DescriptionNew = (props: DescriptionRenderProps) => {
  return (
    <div
      className={cx('te-product-description')}
      style={{ cursor: props.showMoreButton ? 'pointer' : 'auto' }}
      onClick={props.showMoreClicked}
    >
      <span className="te-product-description-title">{t('description')}</span>
      <div
        ref={props.descriptionRef}
        className={cx('te-product-description-content')}
        dangerouslySetInnerHTML={{ __html: marked(props.description || '') }}
        style={{
          height: typeof props.descriptionHeight === 'number' ? `${props.descriptionHeight}px` : 'unset',
        }}
      />
      {props.showMoreButton && (
        <span className="te-product-description-more">{props.descriptionMore ? 'Show Less' : 'Show More'}</span>
      )}
    </div>
  );
};
