import React from 'react';

import { ButtonStyle, ButtonStyleProps } from './button-style';

const DEFAULT_SIZE = 'default';

export function withButtonStyle<T>(
  Component: React.ComponentType<T>,
  displayName = ''
): React.ComponentType<T & ButtonStyleProps> {
  const WrapperComponent = (props: T & ButtonStyleProps) => {
    const { className, size = DEFAULT_SIZE } = props;

    return (
      <ButtonStyle className={className} size={size}>
        {({ classes }) => {
          return React.createElement(Component, { ...props, className: classes });
        }}
      </ButtonStyle>
    );
  };

  WrapperComponent.displayName = displayName;
  return WrapperComponent;
}
