export const shallowObjectCompare = (objA, objB) => {
  const hasOwnProperty = Object.prototype.hasOwnProperty;

  const is = (x, y) => {
    // SameValue algorithm
    if (x === y) {
      // Steps 1-5, 7-10
      // Steps 6.b-6.e: +0 != -0
      return x !== 0 || 1 / x === 1 / y;
    } else {
      // Step 6.a: NaN == NaN
      return x !== x && y !== y;
    }
  };

  if (is(objA, objB)) {
    return true;
  }

  if (typeof objA !== 'object' || objA === null || typeof objB !== 'object' || objB === null) {
    return false;
  }

  var keysA = Object.keys(objA);
  var keysB = Object.keys(objB);

  if (keysA.length !== keysB.length) {
    return false;
  }

  // Test for A's keys different from B.
  for (let i = 0; i < keysA.length; i++) {
    if (!hasOwnProperty.call(objB, keysA[i]) || !is(objA[keysA[i]], objB[keysA[i]])) {
      return false;
    }
  }

  return true;
};

export function getDisplayName<T extends ComponentType>(WrappedComponent: T): string {
  return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

const addCssLine = (css: string, id: string) => {
  const node = document.createElement('style');
  node.id = id;
  node.innerHTML = css;
  document.body.appendChild(node);
};

const createPrimaryColorStyle = (color: string) => {
  return `.te-primary-fill {
        background-color: ${color};
        color: #fff;
    }
    .te-primary-color {
        color: ${color};
    }
    .te-primary-border {
        border-color: ${color};
    }
    .te-primary-border-hover:hover {
        border-color: ${color};
    }`;
};

export function primaryColorManager(primaryColor: /* should be in hex format*/ string) {
  const nodeId = 'primary-color-style';
  return {
    load: () => {
      return addCssLine(createPrimaryColorStyle(primaryColor), nodeId);
    },
    unload: () => {
      const primaryColorStyle = document.getElementById(nodeId);
      primaryColorStyle && primaryColorStyle.parentNode.removeChild(primaryColorStyle);
    },
  };
}

export const delay = (time: number) => new Promise((resolve) => setTimeout(resolve, time));

const configLangToContentfulLocale: { [key in TipserLang]: string } = {
  [TipserLang.deDE]: 'de',
  [TipserLang.svSE]: 'sv-SE',
  [TipserLang.frFR]: 'fr',
  [TipserLang.enUS]: 'en',
  //for newly introduced locales, like fi-FI and nl-NL, we want to use english
  //CF locale, as we reached the limit of available CF locales and we can't add
  //dedicated locales there yet.
  [TipserLang.fiFI]: 'fi-FI',
  [TipserLang.nlNL]: 'en',
  [TipserLang.daDK]: 'en',
};

export function toContentfulCmsLocale(lang: TipserLang): string {
  return configLangToContentfulLocale[lang];
}

export function getVariantWithProductData(variant: TipserProductVariantModel, product: TipserProductModel) {
  const newVariant: TipserProductVariantModel = { ...variant };
  const allKeys = [...new Set([...Object.keys(variant), ...Object.keys(product)])];
  const shouldRewriteObject = (key: string) => variant[key] == null && product[key] != null;
  const shouldRewriteArray = (key: string) =>
    [variant[key], product[key]].every(Array.isArray) && variant[key].length === 0;
  allKeys
    .filter((key) => shouldRewriteObject(key) || shouldRewriteArray(key))
    .forEach((key) => {
      newVariant[key] = product[key];
    });
  return newVariant;
}

const getAttributes = (variant: TipserProductVariantModel) => {
  if (!Array.isArray(variant.attributes)) {
    return [];
  }
  return Object.entries(variant.attributes).reduce((acc, [name, value]) => acc.concat({ name, value }), []);
};

export function mapVariantToAnalyticsLegacyProduct(variant: TipserProductVariantModel) {
  return {
    ...variant,
    name: variant.title,
    attributes: getAttributes(variant),
    price: variant.priceIncVat,
    salesPrice: variant.discountPriceIncVat || variant.priceIncVat,
  };
}
