import React from 'react';

import ButtonCross from '../../common/button-cross/button-cross';
import { customBemNamesFactory } from '../../common/utils';

const bem = customBemNamesFactory('te-popup');

const bemClasses = {
  main: bem(),
  text: bem('text'),
  title: bem('title'),
};

interface ErrorPopupDumbProps {
  onClose: () => void;
  error: string;
  title: string;
}

export function ErrorPopupDumb({ onClose, error, title }: ErrorPopupDumbProps) {
  return (
    <div className={`${bemClasses.main} error`}>
      <div className={bemClasses.title}>{title}</div>
      <div className={bemClasses.text}>{error}</div>
      <ButtonCross onClick={onClose} />
    </div>
  );
}
