import React, { FC, useCallback, useState, useEffect, useMemo } from 'react';

import { Popup } from '../popup';
import { ErrorPopupDumb } from './error-popup-dumb';
import { Contextable } from '../../tipser-context';
import { EVENT_ERROR } from '../../tipser-error';
import { t } from '../../i18n';

export const ErrorPopup: FC<Contextable> = ({ context }) => {
  const [isVisible, setVisibility] = useState(false);
  const errorTitle = useMemo(() => t('default_server_error_title'), []);
  const errorMessage = useMemo(() => t('default_server_error_text'), []);
  const onError = useCallback(() => {
    setVisibility(true);
  }, [setVisibility]);

  useEffect(() => {
    if (context.useDefaultErrorHandler) {
      context.events.register(EVENT_ERROR, onError);
    }
    return () => {
      context.events.unregister(EVENT_ERROR, onError);
    };
  }, [context, onError]);

  const onClose = useCallback(() => {
    setVisibility(false);
  }, [setVisibility]);

  return (
    <Popup isVisible={isVisible} onClose={onClose} name="error-popup">
      <ErrorPopupDumb onClose={onClose} error={errorMessage} title={errorTitle} />
    </Popup>
  );
};
