import { Subscriber } from '.';
import { TrackingPayload } from '../tracking-payload';

const TRACKING_MESSAGE_COMMAND = 'tipser.widget.log';

export class HtmlDocumentSubscriber implements Subscriber {
  constructor() {
    this.listenToEventsFromTiperSdkIframe();
  }

  private listenToEventsFromTiperSdkIframe() {
    window.addEventListener('message', (e) => {
      const data = e.data || {};

      if (data.command === TRACKING_MESSAGE_COMMAND) {
        this.sendEvent(TrackingPayload.fromTipserSdkIframeObject(data.trackData));
      }
    });
  }

  sendEvent<T>(detail: T) {
    try {
      const event = new CustomEvent<T>('tw-track', { detail });
      window.document.dispatchEvent(event);
    } catch (e) {
      // Sorry IE
    }
  }
}
