import React, { ComponentType } from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';

import { ComponentModel } from '../../common/models';
import { PRODUCT_ALTERNATIVE_RENDERER } from './product-renderer';
import { DataTipserViewType, TipserProductComponent } from './tipser-product';
import { withSentry } from '../../common/sentry/with-sentry';
import { TipserProductModel } from './model';
import { Tracking, TrackableProps, withInViewTracker } from '../../common/analytics';
import { getDisplayName } from '../../common/utils';
import { ImageSize } from './product-card-renderer';

import './product.less';

export const COMPONENT_PRODUCT_CONTENT_TYPE = 'product';

export interface ProductModel extends ComponentModel, TrackableProps {
  productId?: string;
  posId?: string;
  renderer?: PRODUCT_ALTERNATIVE_RENDERER;
  imgSize?: ImageSize;
  viewMode?: DataTipserViewType;
  onProductClick?: (product: TipserProductModel) => void;
}
// TODOs: make posId optional

class ProductComponent extends React.Component<ProductModel> {
  render() {
    const { renderer, posId, productId, imgSize, viewMode } = this.props;
    const currentRenderer = renderer
      ? renderer
      : viewMode === 'compact'
      ? PRODUCT_ALTERNATIVE_RENDERER.CARD
      : undefined;

    return (
      <span className="te-link">
        <TipserProductComponent
          posId={posId}
          productId={productId}
          renderer={currentRenderer}
          imgSize={imgSize}
          viewMode={viewMode}
          onProductClick={this.props.onProductClick}
          setTrackingData={this.props.setTrackingData}
        />
      </span>
    );
  }
}

const withStandaloneProductClickTracking = (Component: React.ComponentType<ProductModel>) => {
  class ComponentWithOnClick extends React.PureComponent<ProductModel> {
    static displayName = `WithStandaloneProductClickTracking(${getDisplayName(Component)})`;

    onClick = (product: TipserProductModel) => {
      if (this.props.onProductClick) {
        this.props.onProductClick(product);
      }
    };

    render() {
      return <Component {...this.props} onProductClick={this.onClick} />;
    }
  }
  hoistNonReactStatics(ComponentWithOnClick, Component);
  return ComponentWithOnClick;
};

export const Product: ComponentType<ProductModel> = withSentry(ProductComponent);

export const TrackableProduct: ComponentType<ProductModel> = withSentry(
  withStandaloneProductClickTracking(
    withInViewTracker({
      trackingCallback: (product: TipserProductModel, props: ProductModel) => {
        if (!product) {
          return;
        }
        if (props.viewMode === 'compact') {
          Tracking.dispatchViewStandaloneProduct(product);
        } else {
          Tracking.dispatchViewProductDetails(product);
        }
      },
    })(ProductComponent)
  )
);
