import { isSSR, TipserSDKInstance } from '@tipser/tipser-sdk';

const COMMAND_DISPLAY_PRODUCT = 'tipser.api.displayProduct';
const COMMAND_DIRECT_TO_CHECKOUT = 'tipser.api.directToCheckout';

let postMessageInitialized = false;

export const initPostMessageBannerApi = (tipserSdk: TipserSDKInstance) => {
  if (postMessageInitialized || isSSR()) {
    return;
  }

  window.addEventListener('message', (message) => {
    if (!message.data) {
      return;
    }

    const data = message.data || {};
    switch (data.command) {
      case COMMAND_DISPLAY_PRODUCT:
        tipserSdk.openProductDialog(data.productId);
        break;
      case COMMAND_DIRECT_TO_CHECKOUT:
        tipserSdk.openDirectToCheckoutDialog(data.productId);
        break;
    }
  });

  postMessageInitialized = true;
};
