import { Subscriber } from '.';
import { delay } from '../../utils';
import { getSdkInstance } from '../../../tipser-context';
import { TrackingPayload } from '../tracking-payload';

const firstLoadDelay = delay(2000);

export class WhispererSubscriber implements Subscriber {
  sendEvent(detail: TrackingPayload) {
    const sdkInstance = getSdkInstance();
    const url = location.href;
    firstLoadDelay.then(() => {
      if (!sdkInstance) {
        return;
      }
      const payload = { ...detail, url };

      sdkInstance.analytics.sendWhispererEvent(payload);
    });
  }
}
