import { TrackingPayload } from './tracking-payload';
import { Subscriber } from './subscribers';

export class Analytics {
  private subscribers: Array<Subscriber> = [];

  private sendEvent(event: TrackingPayload) {
    this.subscribers.forEach((subscriber) => subscriber.sendEvent(event));
  }

  registerSubscriber(subscriber: Subscriber) {
    this.subscribers.push(subscriber);
  }

  dispatchEvent(payload: TrackingPayload) {
    this.sendEvent(payload);
  }
}
