import React, { CSSProperties, ImgHTMLAttributes } from 'react';
import cx from 'classnames';

import { thumbImageUrl } from '../images';
import { noop } from '../utils';

import './img.less';

type LazyImgProps = Exclude<ObjectFitImageProps, 'loadingPlaceholderSrc'>;

export const LazyImgCms: React.SFC<LazyImgProps> = (props: LazyImgProps) => (
  <ObjectFitImage {...props} loadingPlaceholderSrc={thumbImageUrl(props.src)} />
);

interface ObjectFitImageProps extends ImgHTMLAttributes<HTMLImageElement> {
  fit?: 'cover' | 'contain' | 'auto'; // If you don't know what is that for, you should use a normal <img> tag.
  loading?: boolean; // This property is required if using onLoad. This one has a pretty poor usability.
  loadingPlaceholderSrc?: string; // if set, will show this image instead of the full quality image from src
  // and load the original image in the background
  backgroundPosition?: string;
}

interface ObjectFitImageState {
  loaded: boolean;
  error: boolean;
}
/**
 * As object-fit is not supported by MS browsers, let's use a wrapper in place of the raw img.
 * Specifying fit prop is required, otherwise there's no point in using the wrapper.
 */
export class ObjectFitImage extends React.Component<ObjectFitImageProps, ObjectFitImageState> {
  image: HTMLImageElement = new Image();
  state = { loaded: false, error: false };

  static defaultProps = {
    fit: 'auto',
  };

  loadImage = () => {
    // the order of attaching onload event handler and image src must be
    // exactly this to assure chrome firing events on every image show,
    // even though any potential cache
    this.image = new Image();
    this.image.onload = () => this.setState({ loaded: true });
    this.image.onerror = () => this.setState({ error: true });
    this.image.src = this.props.src;
  };

  onLoad = () => this.setState({ loaded: true });

  onError = () => this.setState({ error: true });

  componentDidUpdate(oldProps: ObjectFitImageProps) {
    if (this.props.src !== oldProps.src) {
      this.setState({ loaded: false, error: false });
    }
  }

  componentWillUnmount() {
    this.clearOnLoadListener();
  }

  clearOnLoadListener() {
    // There's no normal way to cancel an onload event listener,
    // and every asynchronous code must be cancelled before unmounting
    this.image.onload = noop;
  }

  getStyle(): CSSProperties {
    const { backgroundPosition } = this.props;
    const { loaded } = this.state;
    const backgroundImage = !loaded ? 'none' : `url('${this.props.src}')`;
    const style: CSSProperties = {
      backgroundImage,
    };

    if (backgroundPosition) {
      style.backgroundPosition = backgroundPosition;
    }

    return style;
  }

  render() {
    const { alt, className, fit, src } = this.props;
    const { loaded, error } = this.state;
    const style = this.getStyle();

    const overlayPlaceholder = this.props.loadingPlaceholderSrc !== undefined && (
      <ObjectFitImage
        {...this.props}
        className={cx('img-placeholder', { 'img-placeholder-loaded': this.state.loaded })}
        src={this.props.loadingPlaceholderSrc}
        loadingPlaceholderSrc={undefined}
      />
    );

    return (
      <div
        className={cx('te-img', 'object-fit', `object-fit-${fit}`, {
          'img-loaded': loaded,
          'object-fit-invalid': error,
          [className]: className,
        })}
        style={style}
      >
        {' '}
        <img alt={alt} src={src} onError={this.onError} onLoad={this.onLoad} />
        {overlayPlaceholder}
      </div>
    );
  }
}
