import isBot from 'isbot';

import { TrackingPayload } from './tracking-payload';
import { Analytics } from './analytics';
import { Actions } from './actions';
import { Targets } from './targets';
import { TipserProductModel, PriceModel } from '../../components/product/model';
import { ProductModel } from '../../ecommerce';

interface ProductTrackingPayload {
  id: string;
  name: string;
  merchant?: string;
  brand: string;
  image: string;
  listPriceIncVat: PriceModel;
  salesPriceIncVat: PriceModel;
  categories: Array<{ department: string; section: string; productType: string }>;
  categoriesValue?: string;
  campaign?: string;
  posId?: string;
  quantity?: number;
  deliveryCost: PriceModel;
  variant: Array<{ name: string; value: string }>;
}

const fromProduct = (product: TipserProductModel): ProductTrackingPayload => ({
  id: product.id,
  name: product.title,
  merchant: product.merchantName,
  brand: product.brand,
  image: product.images && product.images[0] && product.images[0]['250x'],
  listPriceIncVat: product.priceIncVat,
  salesPriceIncVat: product.discountPriceIncVat || product.priceIncVat,
  categories: product.categories,
  deliveryCost: product.deliveryCost,
  variant: [],
});

type CollectionViewPayload = [
  {
    id: string;
    items: Array<{ product: TipserProductModel }>;
  }
];

export class TrackingEventDispatcher {
  constructor(private analytics: Analytics) {}

  dispatchCollectionView = (object: CollectionViewPayload) => {
    const payload = new TrackingPayload('Collection viewed', Actions.VIEW, Targets.COLLECTION, object);
    this.analytics.dispatchEvent(payload);
  };

  dispatchCartTabClick = () => {
    const payload = new TrackingPayload('Cart clicked', Actions.CLICK, Targets.CART_TAB, []);
    this.analytics.dispatchEvent(payload);
  };

  dispatchShopView = (object: [{ url_slug: string; items: Array<ProductModel>; id: string }]) => {
    const payload = new TrackingPayload('Shop viewed', Actions.VIEW, Targets.SHOP, object);
    this.analytics.dispatchEvent(payload);
  };

  private dispatchViewProduct = (product: TipserProductModel, description: string, target: Targets) => {
    const payload = new TrackingPayload(description, Actions.VIEW, target, [fromProduct(product)]);
    this.analytics.dispatchEvent(payload);
  };

  dispatchViewProductDetails = (product: TipserProductModel) =>
    this.dispatchViewProduct(product, 'Product detail page viewed', Targets.PRODUCT);

  dispatchViewStandaloneProduct = (product: TipserProductModel) =>
    this.dispatchViewProduct(product, 'Product viewed', Targets.PRODUCT_TILE);

  dispatchClickProduct = (product: TipserProductModel) => {
    const payload = new TrackingPayload('Product clicked', Actions.CLICK, Targets.PRODUCT_TILE, [product]);
    this.analytics.dispatchEvent(payload);
  };

  dispatchCartTabView = () => {
    if (isBot(navigator.userAgent)) {
      return;
    }

    const payload = new TrackingPayload('Cart viewed', Actions.VIEW, Targets.CART_TAB, []);
    this.analytics.dispatchEvent(payload);
  };
}
