import React from 'react';
import { TipserSDK, TipserSDKInstance } from '@tipser/tipser-sdk';
import { Context } from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';
import omitBy from 'lodash.omitby';

import { setLang } from './i18n';
import { createEventBus, EVENT_DIALOG_CLOSED } from './tipser-events';
import { ErrorPopup } from './components/error-popup';
import { SentryClient } from './common/sentry';
import {
  TipserCommonConfigContextData,
  addDefaultValuesToConfig,
  addDefaultContextData,
  TipserContextData,
  TipserElementsConfig,
  DefaultConfigValues,
} from './config';
import { primaryColorManager } from './common/utils';
import { TipserState } from './state-builder';
import { getTipserApiUrl } from './common/tipser-urls';
import { errorEmitter } from './error-emitter';
import { EVENT_ERROR, TipserElementError } from './tipser-error';

import './theme/index.less';
import { initPostMessageBannerApi } from './messages/banner-post-messages-handler';
import { initAddToCartPostMessageHandler } from './messages/add-to-cart-post-messages-handler';

//re-export this type for convenience
export { TipserContextData };

// serves as typed interface for props for components which make use of context
export interface Contextable {
  context?: TipserContextData;
  updateContext?: (value: Partial<TipserCommonConfigContextData>) => void;
}

// singleton context instance
let contextInstance;
let sdkInstance;

const EMPTY_INITIAL_STATE = {
  productsById: {},
  collectionsById: {},
  storeCollections: undefined,
};

export const getSdkInstance = (): TipserSDKInstance => sdkInstance;

// TODO : there is no option to steer connector factory from context config
export const TipserContext = (): Context<{
  updateContext: (value: object) => void;
  data: TipserContextData;
}> => {
  return contextInstance;
};

let contextPreparedData: TipserContextData = undefined;
export const getTipserContextData = () => contextPreparedData;

export type Pushable = { push: (to: string) => void };

export interface TipserElementsProviderProps {
  posId: string;
  config?: TipserElementsConfigProp;
  isSentryEnabled?: boolean;
  initialState?: TipserState;
  history?: Pushable;
  sdkInstance?: TipserSDKInstance;
}

export type TipserElementsConfigProp = Omit<TipserElementsConfig, keyof DefaultConfigValues> &
  Partial<Pick<TipserElementsConfig, keyof DefaultConfigValues>>;

// context provide which allows us to merge predefined context data with new ones
export class TipserElementsProvider extends React.Component<TipserElementsProviderProps, TipserContextData> {
  static defaultProps = {
    config: {},
    isSentryEnabled: true,
  };

  constructor(props: TipserElementsProviderProps) {
    super(props);

    if (props.isSentryEnabled) {
      SentryClient.initialize();
    }

    if (!props.posId) {
      throw new Error('required posId attribute is missing on <TipserElementsProvider/> element');
    }

    const eventBus = createEventBus();

    const configWithDefaultValues = addDefaultValuesToConfig(this.props.config);
    const contextData = addDefaultContextData(configWithDefaultValues, props, eventBus);

    this.state = contextPreparedData = this.addFinalContextData(contextData);

    if (props.config.eventsHandlers) {
      Object.entries(props.config.eventsHandlers).map(([event, handler]) => {
        if (Array.isArray(handler)) {
          handler.map((eventHandler) => eventBus.register(event, eventHandler));
        } else {
          eventBus.register(event, handler);
        }
      });

      //Setting up calling onAddToCart callback for add to card happening inside the old dialog
      initAddToCartPostMessageHandler(props.config.eventsHandlers, this.state.tipserSdk, props.posId, props.config.env);
    }

    initPostMessageBannerApi(this.state.tipserSdk);

    if (!contextInstance) {
      contextInstance = React.createContext(this.state);
    }
  }

  componentDidMount() {
    if (this.state.primaryColor) {
      primaryColorManager(this.state.primaryColor).load();
    }
    this.setState({ initialState: EMPTY_INITIAL_STATE });
    errorEmitter.addErrorListener(this.onError);
  }

  componentWillUnmount() {
    if (this.state.primaryColor) {
      primaryColorManager(this.state.primaryColor).unload();
    }
    errorEmitter.removeErrorListener(this.onError);
  }

  onError = (error?: any) => {
    if (error && error.message) {
      this.state.events.dispatch(EVENT_ERROR, error);
    } else if (error && error.error && error.error.id && error.error.message) {
      this.state.events.dispatch(EVENT_ERROR, new TipserElementError(error.error.id, error.error.message));
    } else {
      console.error(error);
      throw new Error('Unknown error');
    }
  };

  onDialogClosed = () => {
    sdkInstance.getCurrentCartSize().then(
      (cartSize) => {
        this.state.events.dispatch(EVENT_DIALOG_CLOSED, {
          cartSize,
        });
      },
      (e) => {
        console.log('error getting cart size', e);
      }
    );
  };

  private getTipserSdkInstance(config: TipserElementsConfigProp): TipserSDKInstance {
    if (!sdkInstance) {
      const configWithoutNulls = removeNulls(config); // we have to remove null values to have correct defaults from SDK
      sdkInstance = this.props.sdkInstance ? this.props.sdkInstance : TipserSDK(this.props.posId, configWithoutNulls);
      sdkInstance.addDialogClosedListener(this.onDialogClosed);
    }
    return sdkInstance;
  }

  private addFinalContextData = (config: TipserContextData): TipserContextData => {
    const tipserSdk = this.getTipserSdkInstance(this.props.config);
    setLang(tipserSdk.config.lang);
    return {
      ...config,
      ...tipserSdk.config, //tipserSdk may alert config with some of default values, we want to use them in tipser elements
      tipserSdk,
      tipserApiUrl: getTipserApiUrl(tipserSdk.config.env),
    };
  };

  componentDidUpdate(prevProps: TipserElementsProviderProps) {
    if (this.props !== prevProps) {
      this.updateContext(this.props.config);
    }
  }

  private updateContext = (value: Partial<TipserContextData>) => {
    this.setState((prevState: TipserContextData) => {
      contextPreparedData = this.addFinalContextData({ ...prevState, ...value });
      return contextPreparedData;
    });
  };

  render() {
    const TContext = TipserContext();
    return (
      <TContext.Provider value={{ data: this.state, updateContext: this.updateContext }}>
        <ErrorPopup context={this.state} />
        {this.props.children}
      </TContext.Provider>
    );
  }
}

// annotation for react components to inject TipserContext into their props
export const connectedElement = <K extends Contextable>(Component: React.ComponentType<K>) => {
  type PropsWithoutContext = Exclude<K, keyof Contextable>;

  const WrapperComponent = class extends React.Component<PropsWithoutContext> {
    static displayName = `connectedElement(${Component.displayName || Component.name})`;

    render() {
      const TContext = TipserContext();
      return (
        <TContext.Consumer>
          {({ data, updateContext }) => {
            if (!updateContext || !data.env) {
              throw 'Tipser Context Consumer is used without Provider. Have you used <TipserElementsProvider> tag ?';
            }
            return <Component {...this.props} updateContext={updateContext} context={data} />;
          }}
        </TContext.Consumer>
      );
    }
  };
  hoistNonReactStatics(WrapperComponent, Component);
  return WrapperComponent;
};

function removeNulls<T extends object>(object: T): T {
  return omitBy<T>(object, (x) => x == null) as T;
}
