import React from 'react';
import classNames from 'classnames';

import './button-style.less';
import { customBemNamesFactory } from '../../common/utils';

const bemNames = customBemNamesFactory('te-button');

export interface ButtonStyleProps {
  className?: string;
  size?: 'default' | 'small';
}

interface ButtonStyleInternalProps {
  children: (props: { classes: string }) => React.ReactNode;
}

export class ButtonStyle extends React.Component<ButtonStyleProps & ButtonStyleInternalProps> {
  static defaultProps = {
    className: '',
  };

  render() {
    const { children, className, size } = this.props;

    const bemClassNames = bemNames({
      [size]: size,
    });
    const finalClassNames = classNames(bemClassNames, className);
    return children({ classes: finalClassNames });
  }
}
