import { errorEmitter } from './error-emitter';

export const EVENT_ERROR = 'onError';

export interface EventError {
  type?: string;
  id?: string;
  message?: string;
  stack?: string;
}

export class TipserElementError implements EventError {
  type = 'TipserElementError';

  constructor(public id?: string, public message?: string) {
    if (Error.captureStackTrace) {
      // because of IE11...
      Error.captureStackTrace(this, TipserElementError);
    }
  }
}

export const handleError = (error?: any) => errorEmitter.emit(error);
