import React from 'react';
import cx from 'classnames';
import marked from 'marked';
import { t } from '../../i18n';
import { SmallArrowDown } from '../../common/icons';

import './description-classic.less';

interface DescriptionRenderProps {
  toogleDescriptionVisible: () => void;
  descriptionVisible: boolean;
  description: string;
}

export const DescriptionClassic = (props: DescriptionRenderProps) => {
  return (
    <div
      className={cx('te-product-description-classic', { active: props.descriptionVisible })}
      onClick={props.toogleDescriptionVisible}
    >
      <span className="te-product-description-classic-title">
        {t('description')}
        <SmallArrowDown />
      </span>
      <div
        className={cx('te-product-description-classic-content', { active: props.descriptionVisible })}
        dangerouslySetInnerHTML={{ __html: marked(props.description || '') }}
      />
    </div>
  );
};
