import { isSSR } from '@tipser/tipser-sdk';

import { TrackingEventDispatcher } from './tracking-event-dispatcher';
import { Analytics } from './analytics';
import { HtmlDocumentSubscriber, WhispererSubscriber, GaSubscriber } from './subscribers';

const analytics = new Analytics();

if (!isSSR()) {
  analytics.registerSubscriber(new HtmlDocumentSubscriber());
  analytics.registerSubscriber(new WhispererSubscriber());
  analytics.registerSubscriber(new GaSubscriber());
}

export const Tracking = new TrackingEventDispatcher(analytics);

export { withInViewTracker, TrackableProps } from './with-in-view-tracker';
