import React from 'react';
import './loader.less';
import { FadeAnimation } from '../../components/fade-animation/fade-animation';

export interface Props {
  text?: React.ReactNode;
  delayed?: boolean;
}
export class Loader extends React.Component<Props> {
  render(): React.ReactElement {
    return (
      <FadeAnimation delayed={this.props.delayed} fadeIn={true}>
        <div className={'te-loader-pulse-wrapper'}>
          <div className="te-loader-pulse" />
          <span className="te-loader-pulse-text">{this.props.text}</span>
        </div>
      </FadeAnimation>
    );
  }
}
